const RiddleFrameController = require('RiddleFrameController');
const ResultPopup = require('ResultPopup');
const ScoreLabel = require('./component/ScoreLabel');
const constants = require('./constants');
const helpers = require('./lib/helpers');
const riddleGenerate = require('./lib/riddleGenerate');

cc.Class({
    extends: cc.Component,

    properties: {
        riddleFrameCtor: RiddleFrameController,
        resultPopup: ResultPopup,
        notificaitonText: cc.Label,
        resultText: cc.Label,
        timerProgressBar: cc.ProgressBar,
        timerText: cc.Label,
        questtionText: cc.Label,
        scoreText: ScoreLabel,
        chainText: cc.Label
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.rand = require('random-seed').create();
        this.rand.initState();
        this.rand.seed(Math.random());
        this.riddleFrameCtor.node.on('answer', this.onResult, this);
        this.resultPopup.node.on('restart', this.onRestart, this);
    },

    start() {
        this.resetAndStartGame();
        this.resultPopup.node.active = true;
        this.resultPopup.hide(true);
    },

    update(dt) {
        if (this.isPlaying) {
            this.timer -= dt * 1000;
            this.timerText.string = helpers.formatTimer(this.timer + '');
            this.timerProgressBar.progress = this.timer / constants.ROUND_DURATION;

            this.chainTimer -= dt * 1000;
            this.timerProgressBar.barSprite.node.color = new cc.Color(255, this.timer / constants.ROUND_DURATION * 255, this.timer / constants.ROUND_DURATION * 255, 255);

            if (this.timer < 0) {
                this.isPlaying = false;
                this.riddleFrameCtor.locked = true;
                this.resultText.string = 'TIME UP!';
                this.resultText.node.color = new cc.Color().fromHEX('#d35400');
                this.resultText.node.y = -30;
                this.resultText.node.active = true;
                this.resultText.node.opacity = 0;

                cc.tween(this.resultText.node)
                .to(0.3, {position: cc.v2(0, 0), opacity: 255})
                .delay(0.7)
                .call(() => {
                    this.resultPopup.show(this.score);
                }).start();
            }
        }
    },

    resetAndStartGame() {
        this.reset();
        this.animateGoText(() => {
            this.notificaitonText.node.active = false;
            this.riddleFrameCtor.node.active = true;
            this.startNewRiddle();
        });
    },

    reset() {
        this.isPlaying = false;

        this.questionNumber = 1;
        this.chain = 0;
        this.timer = constants.ROUND_DURATION;
        this.chainTimer = 0;
        this.score = 0;
        this.displayingScore = 0;

        this.chainText.node.active = false;
        this.resultText.node.active = false;
        this.riddleFrameCtor.node.active = false;
        this.notificaitonText.node.active = true;
        this.notificaitonText.node.setScale(0);
        this.timerProgressBar.progress = 1;

        this.timerText.string = helpers.formatTimer(this.timer + '');
        this.questtionText.string = 'Question ' + this.questionNumber;
        this.scoreText.setScore(this.score, true);
    },

    animateGoText(cb) {
        cc.tween(this.notificaitonText.node)
            .delay(0.4)
            .call(() => {
                this.notificaitonText.node.setScale(0.3);
                this.notificaitonText.string = 'READY'
            })
            .to(0.35, { scale: { value: 1, easing: 'sineOut' } })
            .delay(0.2)
            .to(0.35, { scale: { value: 0.35, easing: 'sineIn' } })
            .call(() => {
                this.notificaitonText.node.setScale(0.4);
                this.notificaitonText.string = 'GO!'
            })
            .to(0.5, { scale: { value: 1.3, easing: 'sineOut' } })
            .to(0.3, { scale: { value: 1.2, easing: 'sineIn' } })
            .call(() => { cb(); })
            .start();
    },

    nextRiddle () {
        this.questionNumber++;
        this.questtionText.string = 'Question ' + this.questionNumber;
        this.startNewRiddle();
    },

    startNewRiddle() {
        let riddleData = riddleGenerate(this.getCurrentDifficulty(), this.rand);
        this.riddleFrameCtor.optainNewRiddle(riddleData);
        this.isPlaying = true;
        this.chainTimer = constants.CHAIN_DURATION;
    },

    onResult(answerResult) {
        this.riddleFrameCtor.locked = true;
        let isCorrect = answerResult === 'correct';
        this.resultText.string = isCorrect ? 'CORRECT' : 'INCORRECT';
        this.resultText.node.color = new cc.Color().fromHEX(isCorrect ? '#16a085' : '#d35400');
        this.resultText.node.y = -30;
        this.resultText.node.active = true;
        this.resultText.node.opacity = 0;

        //Scoring
        if (isCorrect) {
            if (this.chainTimer > 0) {
                this.chain = Math.min(++this.chain, constants.MAX_CHAIN);
            } else {
                this.chain = 0;
            }
            let gainedScore = constants.SCORING * Math.max(this.chain, 1);
            let animatedStepScore = gainedScore / 6;
            this.score += gainedScore;
            
            cc.tween(this.scoreText.node)
                .delay(0.2)
                .to(0.1, {color: new cc.Color().fromHEX('#ffffff')})
                .call(()=> {
                    this.scoreText.setScore(this.score);
                })
                .to(0.1, {color: new cc.Color().fromHEX('#ffffff')})
                .to(0.1, {color: new cc.Color().fromHEX('#e67e22')})
                .to(0.1, {color: new cc.Color().fromHEX('#ffffff')})
                .to(0.1, {color: new cc.Color().fromHEX('#e67e22')})
                .to(0.1, {color: new cc.Color().fromHEX('#ffffff')})
                .start();

            if (this.chain > 1) {
                this.chainText.node.active = true;
                this.chainText.node.x = this.scoreText.node.width / 2 - 10;
                this.chainText.node.setScale(0);
                this.chainText.string = `Chain x${this.chain}`;
                cc.tween(this.chainText.node)
                    .delay(0.2)
                    .to(0.2, {scale: { value: 1, easing: 'bounceOut' }})
                    .delay(0.3)
                    .to(0.2, {scale: 0})
                    .call(() => {
                        this.chainText.node.active = false;
                    })
                    .start();
            }
        }

        cc.tween(this.resultText.node)
            .to(0.2, {position: cc.v2(0, 0), opacity: 255})
            .delay(0.4)
            .to(0.2, {position: cc.v2(0, -30), opacity: 0})
            .call(() => {
                this.resultText.node.active = false;
                this.riddleFrameCtor.locked = false;
                if (isCorrect) {
                    this.nextRiddle();
                } else {
                    this.riddleFrameCtor.clearInput();
                    this.chainTimer = -1;
                }
            }).start();
    },

    onRestart () {
        this.resultPopup.hide();
        this.resetAndStartGame()
    },

    getCurrentDifficulty () {
        return ~~(this.currentQuestion / constants.DIFFICULTY_STEP) + 1;
    }
});
