//gamplay related
const MULTIPLE_DIVIDE_INCLUDED_DIFFICULTY = 2;

module.exports = function (difficulty, rand) {
  let type = rand.range(difficulty >= MULTIPLE_DIVIDE_INCLUDED_DIFFICULTY ? 4 : 2);
  let riddleType = rand(4);
  let minValue = difficulty * 3;
  let maxValue = difficulty * 10;
  let result = 0;
  let operand1 = 0;
  let operand2 = 0;
  let operator = '';
  if (type === 0) {
    operator = '+';
    result = rand.intBetween(minValue, maxValue);
    operand1 = rand.intBetween(1, result - 1);
    operand2 = result - operand1;
  } else if (type === 1) {
    operator = '-';
    operand1 = rand.intBetween(minValue, maxValue);
    operand2 = rand.intBetween(1, operand1 - 1);
    result = operand1 - operand2;
  } else if (type === 2) {
    operator = '*';
    result = rand.intBetween(minValue, maxValue);
    operand1 = rand.intBetween(2, result / 2 - 2);
    operand2 = Math.round(result / operand1);
    result = operand1 * operand2;
  } else if (type === 3) {
    operator = '/';
    operand1 = rand.intBetween(minValue, maxValue);
    operand2 = rand.intBetween(2, operand1 / 2 - 2);
    result = Math.round(operand1 / operand2);
    operand1 = result * operand2;
  }
  let riddlePart = 'result';
  switch (riddleType) {
    case 0:
      riddlePart = 'result'
      break;
    case 1:
      riddlePart = 'operand1'
      break;
    case 2:
      riddlePart = 'operand2'
      break;
    case 3:
      riddlePart = 'operator'
      break;
  };

  return { 
    result, 
    operand1,
    operand2,
    operator,
    riddlePart
  }
}
