cc.Class({
    extends: cc.Component,

    properties: {
        overlay: cc.Node,
        frame: cc.Node,
        scoreText: cc.Label,
        highScoreText: cc.Label,
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    start () {

    },

    show (score, isHighscore = false) {
        this.overlay.active = true;
        this.frame.active = true;
        this.scoreText.string = 'You scored: ' + score;
        this.highScoreText.node.active = isHighscore;
        
        this.frame.setScale(0.3);

        cc.tween(this.frame)
        .to(0.4, {scale: {value: 1, easing: 'sineInOut'}})
        .start();
    },

    hide (immediate) {
        this.overlay.active = false;
        if (immediate) {
            this.frame.active = false;
            this.frame.setScale(0);
        }else {
            cc.tween(this.frame)
            .to(0.4, {scale: {value: 0, easing: 'sineInOut'}})
            .call(() => {
                this.frame.active = false;
            })
            .start();
        }
    },

    onBackToMenuClicked () {
        cc.director.loadScene("landing");
    },

    onRestartClicked () {
        this.node.emit('restart');
    }

    // update (dt) {},
});
