const FRAME_FOR_SCORE_ANIMATION = 20;

cc.Class({
    extends: cc.Component,

    properties: {
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    start () {
        this.label = this.getComponent(cc.Label);
        this.displayingScore = 0;
        this.lerpingScore = 0;
        this.score = 0;
        this.label.string = `- SCORE: ${this.displayingScore} -`;

        this.lerpingTime = 0;
        this.lerpingValue = 0;
    },

    setScore (value, immediately = false) {
        this.score = value;
        if (immediately) {
            this.lerpingTime = 0;
            this.lerpingValue = 0;
            this.lerpingScore = this.score;
        } else {
            this.lerpingTime = FRAME_FOR_SCORE_ANIMATION;
            this.lerpingValue = (this.score - this.lerpingScore) / FRAME_FOR_SCORE_ANIMATION
        }
    },

    update (dt) {
        if (this.lerpingTime <= 0) {
            this.lerpingValue = 0;
            this.lerpingScore = this.score;
        } else if (this.lerpingTime > 0) {
            this.lerpingScore += this.lerpingValue;
            this.lerpingTime--;
        }
        if (this.displayingScore != this.lerpingScore) {
            this.displayingScore = this.lerpingScore;
            this.label.string = `-SCORE: ${~~this.displayingScore} -`;
        }
    },
});
