// Learn cc.Class:
//  - https://docs.cocos.com/creator/manual/en/scripting/class.html
// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,
    properties: {
        button_0: cc.Button,
        button_1: cc.Button,
        button_2: cc.Button,
        button_3: cc.Button,
        button_4: cc.Button,
        button_5: cc.Button,
        button_6: cc.Button,
        button_7: cc.Button,
        button_8: cc.Button,
        button_9: cc.Button,
        button_Plus: cc.Button,
        button_Minus: cc.Button,
        button_Multiple: cc.Button,
        button_Divide: cc.Button,
        button_C: cc.Button,
        button_Enter: cc.Button,
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.isKeyboardShifed = false;
        this.button_0.node.on('click', this.onButtonClicked, this);
        this.button_1.node.on('click', this.onButtonClicked, this);
        this.button_2.node.on('click', this.onButtonClicked, this);
        this.button_3.node.on('click', this.onButtonClicked, this);
        this.button_4.node.on('click', this.onButtonClicked, this);
        this.button_5.node.on('click', this.onButtonClicked, this);
        this.button_6.node.on('click', this.onButtonClicked, this);
        this.button_7.node.on('click', this.onButtonClicked, this);
        this.button_8.node.on('click', this.onButtonClicked, this);
        this.button_9.node.on('click', this.onButtonClicked, this);
        this.button_Plus.node.on('click', this.onButtonClicked, this);
        this.button_Minus.node.on('click', this.onButtonClicked, this);
        this.button_Multiple.node.on('click', this.onButtonClicked, this);
        this.button_Divide.node.on('click', this.onButtonClicked, this);
        this.button_C.node.on('click', this.onButtonClicked, this);
        this.button_Enter.node.on('click', this.onButtonClicked, this);

        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyPressed, this);
    },

    start () {

    },

    onKeyDown (event) {
        if (event.keyCode === cc.macro.KEY.shift) {
            this.isKeyboardShifed = true;
        }
    },

    onKeyPressed (event) {
        if (event.keyCode === cc.macro.KEY.shift) {
            this.isKeyboardShifed = false;
            return;
        }
        switch(event.keyCode) {
            case cc.macro.KEY['0']:
            case cc.macro.KEY.num0:
                this.node.emit('input', 0);
                break;
            case cc.macro.KEY['1']:
            case cc.macro.KEY.num1:
                this.node.emit('input', 1);
                break;
            case cc.macro.KEY['2']:
            case cc.macro.KEY.num2:
                this.node.emit('input', 2);
                break;
            case cc.macro.KEY['3']:
            case cc.macro.KEY.num3:
                this.node.emit('input', 3);
                break;
            case cc.macro.KEY['4']:
            case cc.macro.KEY.num4:
                this.node.emit('input', 4);
                break;
            case cc.macro.KEY['5']:
            case cc.macro.KEY.num5:
                this.node.emit('input', 5);
                break;
            case cc.macro.KEY['6']:
            case cc.macro.KEY.num6:
                this.node.emit('input', 6);
                break;
            case cc.macro.KEY['7']:
            case cc.macro.KEY.num7:
                this.node.emit('input', 7);
                break;
            case cc.macro.KEY['8']:
                if (this.isKeyboardShifed) {
                    this.node.emit('input', '*');
                    break;
                }
            case cc.macro.KEY.num8:
                this.node.emit('input', 8);
                break;
            case cc.macro.KEY['9']:
            case cc.macro.KEY.num9:
                this.node.emit('input', 9);
                break;
            case cc.macro.KEY["+"]:
                this.node.emit('input', '+');
                break;
            case cc.macro.KEY.dash:
            case cc.macro.KEY["-"]:
                this.node.emit('input', '-');
                break;
            case cc.macro.KEY["*"]:
                this.node.emit('input', '*');
                break;
            case cc.macro.KEY.forwardslash:
            case cc.macro.KEY["/"]:
                this.node.emit('input', '/');
                break;
            case cc.macro.KEY.numdel:
            case cc.macro.KEY.Delete:
            case cc.macro.KEY.backspace:
                this.node.emit('input', 'C');
                break;
            case cc.macro.KEY.equal:
                if (this.isKeyboardShifed) {
                    this.node.emit('input', '+');
                    break;
                }
            case cc.macro.KEY.enter:
            case cc.macro.KEY.space:
            case cc.macro.KEY["="]:
                this.node.emit('input', 'Enter');
                break;
        }
    },

    onButtonClicked (button) {
        switch (button) {
            case this.button_0:
                this.node.emit('input', 0);
                break;
            case this.button_1:
                this.node.emit('input', 1);
                break;
            case this.button_2:
                this.node.emit('input', 2);
                break;
            case this.button_3:
                this.node.emit('input', 3);
                break;
            case this.button_4:
                this.node.emit('input', 4);
                break;
            case this.button_5:
                this.node.emit('input', 5);
                break;
            case this.button_6:
                this.node.emit('input', 6);
                break;
            case this.button_7:
                this.node.emit('input', 7);
                break;
            case this.button_8:
                this.node.emit('input', 8);
                break;
            case this.button_9:
                this.node.emit('input', 9);
                break;
            case this.button_Plus:
                this.node.emit('input', '+');
                break;
            case this.button_Minus:
                this.node.emit('input', '-');
                break;
            case this.button_Multiple:
                this.node.emit('input', '*');
                break;
            case this.button_Divide:
                this.node.emit('input', '/');
                break;
            case this.button_C:
                this.node.emit('input', 'C');
                break;
            case this.button_Enter:
                this.node.emit('input', 'Enter');
                break;
            default:
                cc.error('undefined input button');
        }
    }

    // update (dt) {},
});
