module.exports.isInputOperator = function (value) {
    return value === '+' || value === '-' || value === '*' || value === '/';
};

module.exports.getInputCharacter = function (value) {
    if (value === '/') return 'รท';
    else if (value === '*') return 'x';
    else return value;
}

module.exports.checkAnswer = function (riddle, answer) {
    switch (riddle.riddlePart) {
        case 'operand1':
            return parseInt(answer) === riddle.operand1;
        case 'operand2':
            return parseInt(answer) === riddle.operand2;
        case 'result':
            return parseInt(answer) === riddle.result;
        case 'operator':
            if (answer !== riddle.operator) {
                switch (answer) {
                    case '+':
                        return riddle.operand1 + riddle.operand2 === riddle.result;
                    case '-':
                        return riddle.operand1 - riddle.operand2 === riddle.result;
                    case '*':
                        return riddle.operand1 * riddle.operand2 === riddle.result;
                    case '/':
                        return riddle.operand1 / riddle.operand2 === riddle.result;
                    default:
                        return false;
                }
            } else return true;
    }
    return false;
}
module.exports.formatDurationDHMSObject = function (duration) {
    duration = Math.max(0, duration);
    let seconds = Math.floor(0.5 + duration / 1000);
    let minutes = Math.floor(seconds / 60);
    let hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);

    seconds %= 60;
    minutes %= 60;
    hours %= 24;

    return {
        seconds,
        minutes,
        hours,
        days
    };
}

module.exports.formatTimer = function (time) {
    const remainingTime = this.formatDurationDHMSObject(time);
    const { seconds, minutes, hours, days } = remainingTime;
    let minutesString = minutes > 9 || hours === 0 ? minutes : '0' + minutes;
    let secondString = seconds > 9 ? seconds : '0' + seconds;
    if (days >= 1) {
        return `${days}:${hours}:${minutes}:${seconds}`;
    }
    if (hours >= 1) {
        return `${hours}:${minutesString}:${secondString}`;
    }
    return `${minutesString}:${secondString}`;
}
