var InputFrameController = require('InputFrameController');
var helpers = require('./lib/helpers');

cc.Class({
    extends: cc.Component,

    properties: {
        inputCtor: InputFrameController,
        leftRiddleText: cc.Label,
        rightRiddleText: cc.Label,
        inputText: cc.Label,
        inputFrame: cc.Sprite
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        this.locked = true;
        this.inputCtor.node.on('input', this.onInput, this)
    },

    start () {
    },

    onInput (value) {
        if (this.locked) return;
        if (!this.currentRiddle) return;
        if (!isNaN(value) && this.currentRiddle.riddlePart !== 'operator') {
            if (this.input === null) this.input = 0;
            if (this.input <= 99) {
                this.input = this.input * 10 + value;
                this.inputText.string = this.input > 0 ? this.input.toString() : '';
            }
        } else if (this.currentRiddle.riddlePart === 'operator' && helpers.isInputOperator(value)) {
            this.input = value;
            this.inputText.string = helpers.getInputCharacter(value);
        } else if (value === 'C') {
            this.clearInput();
        } else if (value === 'Enter') {
            this.node.emit('answer', helpers.checkAnswer(this.currentRiddle, this.input) ? 'correct' : 'incorrect');
        }
    },

    clearInput () {
        this.input = null;
        this.inputText.string = '';
    },

    optainNewRiddle (opts) {
        opts = opts || {};
        this.currentRiddle = {};
        this.currentRiddle.operand1 = opts.operand1 || 0;
        this.currentRiddle.operand2 = opts.operand2 || 0;
        this.currentRiddle.result = opts.result || 0;
        this.currentRiddle.operator = opts.operator || '+';
        this.currentRiddle.riddlePart = opts.riddlePart || 'operator';

        this.clearInput();

        const INPUT_FRAME_PADDING = 8;
        const INPUT_FRAME_NUMBERIC = 165;
        const INPUT_FRAME_OPERATOR = 80
        const INPUT_FRAME_WIDTH = this.currentRiddle.riddlePart === 'operator' ? INPUT_FRAME_OPERATOR : INPUT_FRAME_NUMBERIC;

        switch (this.currentRiddle.riddlePart) {
            case 'operand1':
                this.leftRiddleText.string = '';
                this.rightRiddleText.string = this.currentRiddle.operator + ' ' + this.currentRiddle.operand2 + ' = ' + this.currentRiddle.result;
                break;
            case 'operand2':
                this.leftRiddleText.string = this.currentRiddle.operand1 + ' ' + this.currentRiddle.operator;
                this.rightRiddleText.string = '= ' + this.currentRiddle.result;
                break;
            case 'result':
                this.leftRiddleText.string = this.currentRiddle.operand1 + ' ' + this.currentRiddle.operator + ' ' + this.currentRiddle.operand2 + ' =';
                this.rightRiddleText.string = '';
                break;
            case 'operator':
                this.leftRiddleText.string = this.currentRiddle.operand1;
                this.rightRiddleText.string = this.currentRiddle.operand2 + ' = ' + this.currentRiddle.result;
                break;
        }
        this.inputText.string = '';
        this.leftRiddleText._forceUpdateRenderData();
        this.rightRiddleText._forceUpdateRenderData();

        this.inputFrame.node.width = INPUT_FRAME_WIDTH;
        let contentWidth = this.leftRiddleText.node.width + this.rightRiddleText.node.width + this.inputFrame.node.width + INPUT_FRAME_PADDING * 2;

        this.leftRiddleText.node.x = ( this.leftRiddleText.node.width - contentWidth ) / 2;
        this.inputFrame.node.x = this.leftRiddleText.node.x + (this.leftRiddleText.node.width + this.inputFrame.node.width) / 2 + INPUT_FRAME_PADDING;
        this.rightRiddleText.node.x = this.inputFrame.node.x + (this.inputFrame.node.width + this.rightRiddleText.node.width) / 2 + INPUT_FRAME_PADDING * 2;
        this.inputText.node.x = this.inputFrame.node.x;

        this.locked = false;
    },

    // update (dt) {},
});
